<template>
	<view>
		<view class="bar-sticky">
			　　<navigationBar custom="true"> 
					<view class="logo girl">
						胜券在握
						<!-- <image src="../../static/images/logo.jpeg" mode=""></image> -->
					</view>　　　
				　　</navigationBar>
		</view>
		<view class="user-banner flex flex-around">
			<view class="user-banner-left">
				<image :src="info.userInfo.avatarUrl || '../../static/images/user.jpg'" mode=""></image>
				<text>{{info.userInfo.nickName || '游客'}}, 你好</text>
			</view>
			<view class="user-banner-right" >
				<button class="sys_btn" open-type="getUserInfo" lang="zh_CN" @getuserinfo="getUserInfo">{{token != "" && info.userInfo.nickName ? "已授权" : "小程序授权"}}</button>
				</view>
		</view>
		<!-- 订单 -->
		<view class="m20">
			<text>{{orderTitleList[0]}}</text>
			<view class="order-pay flex flex-around">
				<view class="pay-item " v-for="(item,i) in giftOrder" v-key="i" @click="toGoodsOrder(i)">
					 <view class="flex">
						 <icon :class="item.icon" class="pay-icon" :style="{color: item.color}"/>
						 <view v-show="item.tag" class="pay-tag">
						 	1
						 </view>
					 </view>
					 
					 <view class="pay-text" :style="{color: item.color}">{{item.name}}</view>
				</view>
			</view>
		</view>
		<view class="user-bottom">
			<ul>
				<li v-for="(item,index) of bottomList" :key="index">
					<icon :class="item.icon"></icon><text>{{item.name}}</text>
					<view  class="jiantou"><icon class="iconfont icon-youjiantou"></icon></view>
				</li>
			</ul>
		</view>
	</view>
	
</template>

<script>
	import http from '../../common/axios.js'
	import Login from '../../myComponents/login/login.vue'
	import navigationBar from '../../myComponents/navBar/navigationBar.vue'
	export default {
		components: {
			navigationBar,
			Login
		},
		data() {
			return {
				info: {
					userInfo: {},
					code: ''
				},
				orderTitleList: ['礼券订单','充值订单'],
				giftOrder: [
					{
						icon: 'iconfont icon-shouhou',
						name: '全部订单',
						color: '#795DA3',
						tag: false
					},
					{
						icon: 'iconfont icon-daizhifu',
						color: '#eeb173',
						tag: true
					},
					{
						icon: 'iconfont icon-yizhifu',
						name: '已支付',
						color: '#72ab3b',
						tag: true
					},
					{
						icon: 'iconfont icon-yiwancheng',
						name: '已完成',
						color: '#1296db',
						tag: false
					}
				],
				bottomList: [
					{
						icon: 'iconfont icon-tuiguang',
						name: '推广赚钱'
					},
					{
						icon: 'iconfont icon-daijinquan',
						name: '我的代金券'
					},
					{
						icon: 'iconfont icon-xiaoxi',
						name: '我的消息'
					},
					{
						icon: 'iconfont icon-bangzhuzhongxin',
						name: '帮助中心'
					}
				],
				token: ''
			}
		},
		onLoad() {
			// this.getUser()
			// this.init()
			// uni.getSetting({
			// 	success:function(res){
			// 		console.log(res)
			// 	}
			// })
		},
		onShow() {
			this.getUser()
			
		},
		methods:{
			getUser() {
				this.token = uni.getStorageSync('token')
				this.info.userInfo = uni.getStorageSync('my')
				if(!this.token || !this.info.userInfo) return; 
			},
			getUserInfo() {
				uni.showModal({
					title: '温馨提示',
					content: '亲，授权微信登录后才能正常使用小程序功能',
					success: (res) =>{
						console.log(0,res)
						//如果用户点击了确定按钮
						if (res.confirm) {
							this.getUserProfile()
							.then(res =>{
								return this.login()
							})
							.catch(res =>{
								//拒绝授权
								uni.showToast({
									title: '您拒绝了请求,不能正常使用小程序',
									icon: 'error',
									duration: 2000
								});
								return;
							})
							.then(res =>{
								return this.apiLogin()
							})
							.then(res =>{
								console.log('user', res)
								if(res.data.errmsg == '成功' && res.data.data) {
									this.info.userInfo = res.data.data.userInfo
									this.token = res.data.data.token
									uni.setStorageSync('token',this.token)
									uni.setStorageSync('my',this.info.userInfo)
								}						
								return 
							})
							
						} else if (res.cancel) {
							//如果用户点击了取消按钮
							uni.showToast({
								title: '您拒绝了请求,不能正常使用小程序',
								icon: 'error',
								duration: 2000
							});
							return;
						}
					}
				});
			},
			getUserProfile() {
				return new Promise((resolve, reject) =>{
					uni.getUserProfile({
						desc: '获取你的昵称、头像、地区及性别',
						success: res => {
							resolve(res)
						},
						fail: res => {
							reject(res)
						}
						})
					})
			},
			login() {
					return new Promise((resolve, reject) =>{
						uni.login({
						  provider: 'weixin',
						  success:  res => {
							  debugger
							  this.info.code = res.code
							  resolve(res)
						  },
						  fail: res =>{
							  reject(res)
						  }
						});
					})
			},
			apiLogin() {
				return new Promise((resolve, reject) =>{
					uni.request({
						url: getApp().globalData.apiUrl + '/demo/wx/auth/login_by_weixin',
						method: 'POST',
						data: this.info,
						success: (res) => {
							debugger
							resolve(res)
						},
						fail: (res) => {
							reject(res)
						}
					})
				})
			},
			toGoodsOrder(index) {
				uni.navigateTo({
					url: '../orders/goodsOrder?index='+index
				})
			}
		}
	}
</script>

